import React, { useState, useEffect } from "react";
import { Cell, Div, Avatar, Link, Button } from "@vkontakte/vkui";
import Icon16Up from "@vkontakte/icons/dist/16/up";


import { StatisticsTargetGroup } from "../panels";

const GroupItem = ({
  groupUrl,
  groupId,
  profitToday,
  profitAllTime,
  name,
  photo,
  go,
}) => {
  return (
    <Cell
      before={
        groupUrl ? (
          <Link href={groupUrl} target="_blank">
            <Avatar src={photo} />
          </Link>
        ) : (
          <Avatar src={"https://vk.com/images/camera_200.png"} />
        )
      }
      description={
        <>
          <Div
            style={{
              display: "flex",
              alignItems: "start",
              flexDirection: "row",
              justifyContent: "space-between",
              marginTop: 5,
              marginBottom: 5,
              marginLeft: 10,
              padding: 0,
            }}
          >
            <Div style={{ marginBottom: 10 }}>
              За сегодня: {profitToday} руб
            </Div>
            <Div>За все время: {profitAllTime} руб</Div>
          </Div>
          <Div>
            <Button
              // id={groupId}
              size="m"
              onClick={go}
              data-to="group-info-statistic"
              stretched
              style={{
                backgroundColor: "green",
                color: "white",
                borderRadius: "6px",
                // width: "120px",
                marginLeft: 20,
              }}
            >
              Статистика
            </Button>
          </Div>
        </>
      }
    >
      <Div style={{ marginTop: 4, marginLeft: 20 }}>{name}</Div>
    </Cell>
  );
};

export { GroupItem };
