import React from "react";
import { SimpleCell, Button, Div, Link } from "@vkontakte/vkui";

import { StickerPackIcon } from "../components";

const StickersAllItem = ({
  id,
  name,
  price,
  icon,
  authorId,
  authorName,
  description,
  amount,
  openModalStickers,
  openPaymentSystem,
  qiwiPay,
  fetchedUser,
}) => {
  return (
    <SimpleCell
      before={
        <Div id={id} onClick={() => openModalStickers(id)}>
          <StickerPackIcon icon={icon}></StickerPackIcon>
        </Div>
      }
      // after={
      //   <Link id={id} mode="tertiary">
      //     купить
      //   </Link>
      // }
      description={
        <>
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              flexDirection: "row",
              justifyContent: "start",
              marginTop: 5,
              marginBottom: 5,
              padding: 0,
            }}
          >
            <Link href={`https://vk.com/id${authorId}`} target="_blank">
              {authorName}
            </Link>
          </Div>
          <Button
            size="m"
            onClick={() => qiwiPay(price, id)}
            style={{
              backgroundColor: "green",
              color: "white",
              borderRadius: "6px",
              width: "120px",
            }}
          >
            Купить за {price} ₽
          </Button>
        </>
      }
    >
      {name}
    </SimpleCell>
  );
};

export { StickersAllItem };
