import React from "react";
import { SimpleCell, Button, Div, Link } from "@vkontakte/vkui";

// import { StickerPackIcon } from '../components'

const SliderItem = ({
id,
openModalStickers,
className
}) => {
  return (
    <Div
    id={id}
    onClick={() => openModalStickers(id)}
    className={className}
  >
    
  </Div>
  );
};

export { SliderItem };
