import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import {
  Panel,
  PanelHeaderSimple,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  HorizontalScroll,
  Group,
  Gallery,
  Header,
  Link,
  Button,
} from "@vkontakte/vkui";

import { StickersAllItem, SliderItem, StickersItem } from "../items";
import { SocketManager, dataSlider } from "../helpers";
import { ModalStickers } from "../modals";

const { callMethod } = SocketManager;

const Shop = ({
  id,
  stub,
  snackbar,
  setActiveModal,
  openPaymentSystem,
  qiwiPay,
  connectTwoDevices,
  socketReconnect,
  AddToCommunity,
}) => {
  let targetId = 0;
  // let dataStickers = [];

  const [activeTab, setActiveTab] = useState("all-stickers");
  const [slideIndex, setSlideIndex] = useState(1);
  const [dataStickers, setDataStickers] = useState([]);

  // RENDERS
  // const [renderDataStickersAll, setRenderDataStickersAll] = useState([]);

  useEffect(() => {
    getAllStickers();
    setTimeout(() => {
      console.log("useEffect allStickers ", dataStickers);
    }, 5000);
  }, []);

  function openModalStickers(id) {
    // targetId = +e.currentTarget.id;
    // console.log("targetId", targetId);

    console.log("id: ", id);

    const {
      name,
      author,
      description,
      tags,
      price,
      icon,
      stickersCount,
    } = dataStickers.find((stickers) => stickers.id === +id);

    setActiveModal(
      <ModalStickers
        name={name}
        price={price}
        icon={icon}
        author={author}
        description={description}
        tags={tags}
        stickersCount={stickersCount}
        setActiveModal={setActiveModal}
        openPaymentSystem={openPaymentSystem}
      />
    );
  }

  function tab(type) {
    setActiveTab(type);
    window.scrollTo(0, 0);

    getAllStickers(type === "all-stickers" ? [] : [type]);
  }

  function getAllStickers(tags = []) {
    return callMethod("getStickerPacks", {
      tags,
    }).then(({ message }) => {
      setDataStickers(message);
      // dataStickers = message;
      console.log("getAllStickers message", message);
      console.log("getAllStickers dataStickers", dataStickers);
    });
  }

  const renderDataStickersAll = dataStickers.map((item, index) => {
    return (
      <StickersAllItem
        key={index}
        id={item.id}
        name={item.name}
        authorId={item.authorId}
        authorName={item.authorName}
        description={item.description}
        price={item.price}
        icon={item.icon}
        amount={item.amount}
        openModalStickers={openModalStickers}
        qiwiPay={qiwiPay}
      />
    );
  });

  const renderSliderItem = dataSlider.map((item, index) => {
    return (
      <SliderItem
        key={index}
        id={item.id}
        className={item.className}
        openModalStickers={openModalStickers}
      />
    );
  });

  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple
        left={
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              flexDirection: "row",
            }}
          >
            <Button
              style={{
                backgroundColor: "green",
                borderRadius: "9px",
                marginLeft: "10px",
              }}
              mode="commerce"
              size="m"
              onClick={AddToCommunity}
            >
              Добавить в группу
            </Button>
            <Button
              style={{
                backgroundColor: "#3F8AE0",
                borderRadius: "9px",
                marginLeft: "10px",
              }}
              mode="primary"
              size="m"
              // onClick={AddToCommunity}
              href="https://vk.me/kirill_1131" target="_blank"
              
            >
              Авторам
            </Button>
          </Div>
        }
      ></PanelHeaderSimple>

      <Gallery
        // timeout="2000"
        slideIndex={slideIndex}
        onChange={(slideIndex) => setSlideIndex(slideIndex)}
        slideWidth="380px"
        align="center"
        style={{
          height: "180px",
          marginTop: "10px",
        }}
      >
        {renderSliderItem}
      </Gallery>

      <>
        <HorizontalScroll>
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "space-between",
              flexDirection: "row",
              margin: 10,
              userSelect: "none",
              // marginRight: 20,
              // marginLeft: 20,
            }}
          >
            <Div
              onClick={() => tab("all-stickers")}
              className={activeTab === "all-stickers" ? "tabGreen" : "tabWhite"}
            >
              Все
            </Div>
            <Div
              onClick={() => tab("anime")}
              className={activeTab === "anime" ? "tabGreen" : "tabWhite"}
            >
              Аниме
            </Div>
            <Div
              onClick={() => tab("games")}
              className={activeTab === "games" ? "tabGreen" : "tabWhite"}
            >
              Игры
            </Div>
            <Div
              onClick={() => tab("cartoons")}
              className={activeTab === "cartoons" ? "tabGreen" : "tabWhite"}
            >
              Мультики
            </Div>
            <Div
              onClick={() => tab("films")}
              className={activeTab === "films" ? "tabGreen" : "tabWhite"}
            >
              Фильмы
            </Div>
            <Div
              onClick={() => tab("tv-series")}
              className={activeTab === "tv-series" ? "tabGreen" : "tabWhite"}
            >
              Сериалы
            </Div>
          </Div>
        </HorizontalScroll>
      </>

      <>
        {connectTwoDevices ? (
          stub(
            "Вы подключились с 2 устройств ",
            socketReconnect,
            "Переподключиться"
          )
        ) : (
          <>
            {activeTab === "all-stickers" ? (
              // <StickersItem renderDataStickersAll={renderDataStickersAll} />
              <Div>{renderDataStickersAll}</Div>
            ) : (
              ""
            )}

            {activeTab === "anime" ? (
              <StickersItem renderDataStickersAll={renderDataStickersAll} />
            ) : (
              ""
            )}

            {/* {activeTab === "games" ? (
              <StickersItem renderDataStickersAll={renderDataStickersAll} />
            ) : (
              ""
            )} */}

            {activeTab === "cartoons" ? (
              <StickersItem renderDataStickersAll={renderDataStickersAll} />
            ) : (
              ""
            )}

            {activeTab === "films" ? (
              <StickersItem renderDataStickersAll={renderDataStickersAll} />
            ) : (
              ""
            )}

            {activeTab === "tv-series" ? (
              <StickersItem renderDataStickersAll={renderDataStickersAll} />
            ) : (
              ""
            )}

            {snackbar}
          </>
        )}
      </>
    </Panel>
  );
};

export { Shop };
