import React, { useState, useEffect } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  Group,
  Header,
  Cell,
  Div,
  Link,
  InfoRow,
  Input,
  FormLayoutGroup,
  FormLayout,
  Button,
  Spinner
} from "@vkontakte/vkui";
import Icon28WriteOutline from "@vkontakte/icons/dist/28/write_outline";

import { OperationsHistoryItem } from "../items";
import { dataHistory, SocketManager } from "../helpers";

const { callMethod } = SocketManager;

const Statistics = ({ openAlert }) => {
  const [groupName, setGroupName] = useState("");
  const [amountToday, setAmountToday] = useState(0);
  const [amountAllTime, setAmountAllTime] = useState(0);
  const [donations, setDonations] = useState([]);
  const [groupBalance, setGroupBalance] = useState(0);

  function getStats() {
    callMethod("getStatsForGroup").then(({ message }) => {
      console.log("getStatsForGroup", message);
      console.log("message.groupName", message.groupName);

      setGroupName(message.groupName);
      setAmountToday(message.amountToday);
      setAmountAllTime(message.amountAllTime);
      setDonations(message.donations);
      setGroupBalance(message.groupBalance);
    });
  }

  useEffect(() => {
    getStats();
  }, []);

  function formatDate(date) {
    date = new Date(date * 1000);

    let dd = date.getDate();
    if (dd < 10) dd = "0" + dd;
    let mm = date.getMonth() + 1;
    if (mm < 10) mm = "0" + mm;
    let yy = date.getFullYear() % 100;
    if (yy < 10) yy = "0" + yy;
    let hh = date.getHours();
    if (hh < 10) hh = "0" + hh;
    let minutes = date.getMinutes();
    if (minutes < 10) minutes = "0" + minutes;

    return dd + "." + mm + "." + yy + " " + hh + ":" + minutes;
  }

  const renderHistory = donations.map((item, index) => {
    const senderUrl = "https://vk.com/id" + item.userId;

    return (
      <OperationsHistoryItem
        key={index}
        id={index + 1}
        senderUrl={senderUrl}
        amount={item.amount}
        senderPhoto={item.userPhotoURL}
        createdAt={item.createdAt}
        formatDate={formatDate}
      />
    );
  });

  return (
    <>
      {!renderHistory.length ? (
        <div className="spinner">
          <Spinner size="large" style={{ marginTop: 90 }} />
        </div>
      ) : (
        <Div
          style={{
            margin: 10,
          }}
        >
          <Cell>
            <Div>
              <Group separator="show">
                <Div
                  style={{
                    marginBottom: 5,
                    marginTop: 5,
                    marginLeft: 15,
                  }}
                >
                  Статистика по группе:
                </Div>
                <Div
                  style={{
                    marginBottom: 5,
                    marginTop: 5,
                    marginLeft: 15,
                  }}
                >
                  {groupName}
                </Div>
              </Group>
              <Group separator="show">
                <Div
                  style={{
                    marginBottom: 5,
                    marginTop: 5,
                    marginLeft: 15,
                  }}
                >
                  За сегодня: {amountToday} руб
                </Div>
                <Div
                  style={{
                    marginBottom: 5,
                    marginTop: 5,
                    marginLeft: 15,
                  }}
                >
                  За все время: {amountAllTime} руб
                </Div>
                <Div
                  style={{
                    marginBottom: 5,
                    marginTop: 5,
                    marginLeft: 15,
                  }}
                >
                  Баланс: {groupBalance} руб
                </Div>

                <Div
                  style={{
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "start",
                    flexDirection: "row",
                  }}
                >
                  <Button
                    size="m"
                    onClick={getStats}
                    style={{
                      marginBottom: 5,
                      marginTop: 5,
                      marginLeft: 15,
                      backgroundColor: "green",
                      color: "white",
                      borderRadius: "6px",
                      width: "160px",
                    }}
                  >
                    Обновить данные
                  </Button>
                  <Button
                    size="m"
                    onClick={getStats}
                    style={{
                      marginBottom: 5,
                      marginTop: 5,
                      marginLeft: 15,
                      backgroundColor: "#3F8AE0",
                      color: "white",
                      borderRadius: "6px",
                      width: "160px",
                    }}
                  >
                    Вывести
                  </Button>
                </Div>
              </Group>

              {renderHistory}
            </Div>
          </Cell>
        </Div>
      )}
    </>
  );
};

export { Statistics };
