import React from "react";
import {
  Div,
  Spinner
} from "@vkontakte/vkui";

const StickersItem = ({renderDataStickersAll}) => {
  return (
    <Div>
      {!renderDataStickersAll.length ? (
        <div
          style={{
            display: "flex",
            alignItems: "center",
            flexDirection: "column",
          }}
        >
          <Spinner size="large" style={{ marginTop: 30 }} />
        </div>
      ) : (
        <Div
          style={{
            paddingLeft: 10,
            paddingRight: 10,
          }}
        >
          
          {renderDataStickersAll}
        </Div>
      )}
    </Div>
  );
};

export { StickersItem };
