import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  FixedLayout,
  Tabs,
  TabsItem,
  Div,
  Spinner,
  HorizontalScroll,
  Group,
  Gallery,
  Header,
} from "@vkontakte/vkui";

import { Statistics } from "../components";

const GroupInfoStatistic = ({
  id,
  go,
  stub,
  snackbar,
  openAlert,
  connectTwoDevices,
  socketReconnect,
}) => {
  return (
    <Panel id={id} separator={false}>
      <PanelHeaderSimple
      left={
        <PanelHeaderBack onClick={go} data-to="profile" />
      }
      >
        Статистика по группе
        </PanelHeaderSimple>

      <>
      {connectTwoDevices ? (
            stub(
              "Вы подключились с 2 устройств ",
              socketReconnect,
              "Переподключиться"
            )
        ) : (
          <>
            <Div>
              <Statistics 
              openAlert={openAlert}
              />
            </Div>
            {snackbar}
          </>
        )}
      </>
    </Panel>
  );
};

export { GroupInfoStatistic };
