import React, { useState, useEffect } from "react";
import bridge from "@vkontakte/vk-bridge";
import {
  View,
  ScreenSpinner,
  ConfigProvider,
  ActionSheetItem,
  Avatar,
  Snackbar,
  CellButton,
  Div,
  Epic,
  Tabbar,
  TabbarItem,
  ActionSheet,
} from "@vkontakte/vkui";
import "@vkontakte/vkui/dist/vkui.css";
import Icon28Favorite from "@vkontakte/icons/dist/28/favorite";
import Icon28MarketOutline from "@vkontakte/icons/dist/28/market_outline";
import Icon28UserCircleOutline from "@vkontakte/icons/dist/28/user_circle_outline";
import Icon16Done from "@vkontakte/icons/dist/16/done";
import Icon16Cancel from "@vkontakte/icons/dist/16/cancel";

import "./styles/reset.sass";
import "./styles/panels.sass";
import "./styles/components.sass";
import "./styles/icons.sass";

import { Shop, Profile, GroupInfoStatistic, StatisticsTargetGroup } from "./panels";

import { SocketManager } from "./helpers";

const socket = SocketManager.createConnection();

const App = () => {
  let targetId = 0;
  const [isConnected, setIsConnected] = useState(true);
  const [isAdmin, setIsAdmin] = useState(false);
  const [isSuperAdmin, setIsSuperAdmin] = useState(false);
  const [activePanel, setActivePanel] = useState("shop");
  const [fetchedUser, setUser] = useState(null);
  const [popout, setPopout] = useState(<ScreenSpinner size="large" />);
  const [networkStatus, setNetworkStatus] = useState(false);
  const [snackbar, setSnackbar] = useState(null);
  const [activeModal, setActiveModal] = useState(null);
  const [connectTwoDevices, setConnectTwoDevices] = useState(false);

  // useEffect(() => {
  //   bridge
  //     .sendPromise("VKWebAppShowNativeAds", {
  //       ad_format: "preloader",
  //     })
  //     .then((res) => console.log("VKWebAppShowNativeAds RES", res))
  //     .catch((err) => console.log("VKWebAppShowNativeAds ERR", err));
  // }, []);

  useEffect(() => {
    bridge.subscribe(({ detail: { type, data } }) => {
      if (type === "VKWebAppUpdateConfig") {
        const schemeAttribute = document.createAttribute("scheme");
        schemeAttribute.value = data.scheme ? data.scheme : "bright_light";
        document.body.attributes.setNamedItem(schemeAttribute);
      }
    });
    function fetchData() {
      bridge.send("VKWebAppGetUserInfo").then((user) => {
        setUser(user);
        console.log(user);
        window.userId = user.id;
        setPopout(null);
      });
    }
    fetchData();

    window.addEventListener("popstate", (e) => e.preventDefault() & menu(e));
  }, []);

  useEffect(() => {
    const params = new URLSearchParams(document.location.search);

    if (
      params.has("vk_viewer_group_role") &&
      params.get("vk_viewer_group_role") === "admin"
    ) {
      setIsAdmin(true);
    }

    if ([236908027, 73606509].includes(+params.get("vk_user_id"))) {
      setIsAdmin(true);
      setIsSuperAdmin(true);
    }

    socket.on("multisession", {
      //
    });

    socket.on("disconnect", () => {
      setIsConnected(false);
    });
  }, []);

  function socketReconnect() {
    console.log(
      "isConnected:",
      isConnected,
      "setConnectTwoDevices:",
      connectTwoDevices
    );

    setIsConnected(true);
    setConnectTwoDevices(false);
    socket.open();
  }

  function menu(e) {
    setActivePanel("shop");
  }

  function openPaymentSystem(e, price) {
    setActiveModal(null);
    setPopout(
      <>
        <ActionSheet onClose={() => setPopout(null)}>
          {/* <ActionSheetItem autoclose onClick={() => qiwiPay(e,price)}>
              Оплатить через QIWI
            </ActionSheetItem> */}
          <ActionSheetItem autoclose onClick={() => payVkPay(e, price)}>
            Оплатить через VK Pay
          </ActionSheetItem>
        </ActionSheet>
      </>
    );
  }

  function qiwiPay(price, packId) {
    bridge
      .sendPromise("VKWebAppAllowMessagesFromGroup", {
        group_id: 194979339,
      })
      .then((res) => {
        console.log(res);
        window.QiwiCheckout.createInvoice({
          publicKey:
            "48e7qUxn9T7RyYE1MVZswX1FRSbE6iyCj2gCRwwF3Dnh5XrasNTx3BGPiMsyXQFNKQhvukniQG8RTVhYm3iP6eNeguoXyC99Aq22J8SZPKNgc2WqKx64qaPHePwqThccSrj3HPYcWtCAAb4y8zT5JD6kUUSyJK1sVaG2XrP9hXNzXzo5zVD2iWC9o81QD",
          amount: price,
          comment: `stickers ${window.userId} ${packId}`,
        })
          .then((response) => {
            console.log("qiwi", response);
          })
          .catch((error) => {
            console.log("QIWI ERROR:", error);
          });
      });
  }

  function payVkPay(e, price) {
    bridge
      .sendPromise("VKWebAppOpenPayForm", {
        app_id: 7448001,
        action: "pay-to-group",
        params: {
          amount: +price,
          group_id: 194979339,
        },
      })
      .catch((error) => {
        console.log(error);
      });
  }

  function openAlert(text, backgroundColor = "green") {
    if (snackbar) return;
    setSnackbar(
      <Snackbar
        duration="7000"
        layout="horizontal"
        onClose={() => setSnackbar(null)}
        before={
          <Avatar size={24} style={{ backgroundColor }}>
            {backgroundColor === "green" ? (
              <Icon16Done fill="#fff" width={14} height={14} />
            ) : (
              <Icon16Cancel fill="#fff" width={14} height={14} />
            )}
          </Avatar>
        }
      >
        {text}
      </Snackbar>
    );
  }

  const stub = (textDiv, retryFunction, textButton) => {
    return (
      <Div className="divSpinner">
        <Div className="div">{textDiv}</Div>
        <Div className="div">
          <CellButton onClick={retryFunction}>{textButton}</CellButton>
        </Div>
      </Div>
    );
  };

  function AddToCommunity(e) {
    bridge
      .send("VKWebAppAddToCommunity", {})
      .then((res) => {
        console.log(" AddToCommunity res", res);
      })
      .catch((err) => {
        console.log(" AddToCommunity err", err);
      });
  }

  const go = (e) => {
    setActivePanel(e.currentTarget.dataset.to);
    console.log("123");
  };

  return (
    <ConfigProvider isWebView={true}>
      <Epic
        tabbar={
          <Tabbar>
            <TabbarItem onClick={go} data-to="shop" text="Стикеры">
              {activePanel === "shop" ? (
                <Icon28Favorite fill="green" />
              ) : (
                <Icon28Favorite />
              )}
            </TabbarItem>

            {fetchedUser && (
              <>
                {isAdmin ? (
                  <TabbarItem
                    onClick={() => setActivePanel("profile")}
                    data-to="profile"
                    text="Профиль"
                  >
                    {activePanel === "profile" ? (
                      <Icon28UserCircleOutline fill="green" />
                    ) : (
                      <Icon28UserCircleOutline />
                    )}
                  </TabbarItem>
                ) : (
                  ""
                )}
              </>
            )}
          </Tabbar>
        }
      >
        <View activePanel={activePanel} popout={popout} modal={activeModal}>
          <Shop
            id="shop"
            fetchedUser={fetchedUser}
            go={go}
            stub={stub}
            openAlert={openAlert}
            snackbar={snackbar}
            setActiveModal={setActiveModal}
            openPaymentSystem={openPaymentSystem}
            qiwiPay={qiwiPay}
            connectTwoDevices={connectTwoDevices}
            socketReconnect={socketReconnect}
            AddToCommunity={AddToCommunity}
          />
          <Profile
            id="profile"
            go={go}
            fetchedUser={fetchedUser}
            openAlert={openAlert}
            snackbar={snackbar}
            connectTwoDevices={connectTwoDevices}
            socketReconnect={socketReconnect}
            isSuperAdmin={isSuperAdmin}
          />
          <GroupInfoStatistic
            id="group-info-statistic"
            go={go}
            openAlert={openAlert}
            snackbar={snackbar}
            connectTwoDevices={connectTwoDevices}
            socketReconnect={socketReconnect}
            stub={stub}
          />
          <StatisticsTargetGroup
            id="statistics-target-group"
            go={go}
            openAlert={openAlert}
            snackbar={snackbar}
            connectTwoDevices={connectTwoDevices}
            socketReconnect={socketReconnect}
            stub={stub}
          />
        </View>
      </Epic>
    </ConfigProvider>
  );
};

export default App;
