import React, { useState, useEffect } from "react";
import {
  Panel,
  PanelHeaderSimple,
  PanelHeaderBack,
  Group,
  Header,
  Cell,
  Div,
  Link,
  InfoRow,
  Input,
  FormLayoutGroup,
  FormLayout,
  Button,
} from "@vkontakte/vkui";
import Icon28WriteOutline from "@vkontakte/icons/dist/28/write_outline";

import { GroupItem } from "../items";
import { dataGroups, SocketManager } from "../helpers";

const { callMethod } = SocketManager;

const StatisticsGroups = ({go}) => {
  const [dataOperationsHistory, setDataOperationsHistory] = useState([]);
  const [amountToday, setAmountToday] = useState(0);
  const [amountAllTime, setAmountAllTime] = useState(0);
  const [groups, setGroups] = useState([]);

  function getStats() {
    callMethod('getStatsForGroups').then(({ message}) => {
      setAmountToday(message.amountToday);
      setAmountAllTime(message.amountAllTime);
      setGroups(message.groups);
    })
  }

  useEffect(() => {
    getStats();
  }, []);

  function formatDate(date) {
    date = new Date(date * 1000);

    let dd = date.getDate();
    if (dd < 10) dd = "0" + dd;
    let mm = date.getMonth() + 1;
    if (mm < 10) mm = "0" + mm;
    let yy = date.getFullYear() % 100;
    if (yy < 10) yy = "0" + yy;
    let hh = date.getHours();
    if (hh < 10) hh = "0" + hh;
    let minutes = date.getMinutes();
    if (minutes < 10) minutes = "0" + minutes;

    return `${dd}.${mm}.${yy} ${hh}:${minutes}`;
  }

  const renderGroups = groups.map((item, index) => {
    const groupUrl = "https://vk.com/club" + item.groupId;

    return (
      <GroupItem
        key={index}
        id={index + 1}
        go={go}
        groupUrl={groupUrl}
        groupId={item.groupId}
        profitToday={item.amountToday}
        profitAllTime={item.amountAllTime}
        name={item.groupName}
        photo={item.groupPhotoURL}
        formatDate={formatDate}
        donations={item.donations}
      />
    );
  });

  return (
    <Div
      style={{
        margin: 10,
      }}
    >
      <Cell>
        <Div>
          <Group separator="show">
            <Div
              style={{
                marginBottom: 5,
                marginTop: 5,
                marginLeft: 15,
              }}
            >
              Статистика по всем группам:
            </Div>
          </Group>
          <Group separator="show">
            <Div
              style={{
                marginBottom: 5,
                marginTop: 5,
                marginLeft: 15,
              }}
            >
              За сегодня: {amountToday} руб
            </Div>
            <Div
              style={{
                marginBottom: 5,
                marginTop: 5,
                marginLeft: 15,
              }}
            >
              За все время: {amountAllTime} руб
            </Div>

            <Button
              size="m"
              onClick={getStats}
              style={{
                marginBottom: 5,
                marginTop: 5,
                marginLeft: 15,
                backgroundColor: "green",
                color: "white",
                borderRadius: "6px",
                width: "160px",
              }}
            >
              Обновить данные
            </Button>
          </Group>

          {renderGroups}
        </Div>
      </Cell>
    </Div>
  );
};

export { StatisticsGroups };
